/**
 * External dependencies
 */
import { Picture } from 'react-responsive-picture';

/** 
 * WordPress dependencies
 */
import { Fragment } from '@wordpress/element';
import { addQueryArgs } from '@wordpress/url';

const A1 = {
    default: '564,317',
    small: '690,388',
    hidpi: '1128,634',
    smallHidpi: '1380,776',
};

const A2 = {
    default: '268,151',
    small: '690,388',
    hidpi: '536,301',
    smallHidpi: '1380,776',
};

const A3 = {
    default: '194,110',
    small: '148,84',
    hidpi: '388,220',
    smallHidpi: '296,168',
};

const A4 = {
    default: '268,151',
    small: '690,388',
    hidpi: '536,302',
    smallHidpi: '1380,776',
};

const XL = {
    default: '720,405',
    small: '690,388',
    hidpi: '1440,810',
    smallHidpi: '1380,776',
};

const legacy = {
    '260': {
        default: '260,260',
        small: '260,260',
        hidpi: '520,520',
        smallHidpi: '520,520',
    },
    '260-173': {
        default: '260,173',
        small: '260,173',
        hidpi: '520,346',
        smallHidpi: '520,346',
    },
};

const ImageDisplay = ({ img, size, link, onClick = false, placeholder = false }) => {
    // eslint-disable-next-line no-shadow
    const getImgURL = variant => {
        if (true === placeholder) {
            let dimensions = '';
            if ('A2' === size) {
                dimensions = '536x301';
            } else if ('A3' === size) {
                dimensions = '388x220';
            } else if ('A4' === size) {
                dimensions = '536x302';
            } else if ('XL' === size) {
                dimensions = '1440x810';
            } else if ('legacy-260' === size) {
                // Default to A1
                dimensions = '260x260';
            } else if ('legacy-260-173' === size) {
                // Default to A1
                dimensions = '260x173';
            } else {
                // Default to A1
                dimensions = '1128x634';
            }
            return `https://via.placeholder.com/${dimensions}.png?text=${size}`;
        }

        if ('' === img || false === img) {
            return img;
        }

        // Default to A1
        let args = { resize: A1[variant] };
        if ('A2' === size) {
            args = { resize: A2[variant] };
        } else if ('A3' === size) {
            args = { resize: A3[variant] };
        } else if ('A4' === size) {
            args = { resize: A4[variant] };
        } else if ('XL' === size) {
            args = { resize: XL[variant] };
        }

        // Temp legacy sizes for homepages
        if ('legacy-260' === size) {
            args = { resize: legacy['260'][variant] };
        } else if ('legacy-260-173' === size) {
            args = { resize: legacy['260-173'][variant] };
        }

        return addQueryArgs(img, args);
    };

    // eslint-disable-next-line no-shadow
    const getImgSrcSet = (threshold = 767) => {
        return [
            {
                srcSet: `${getImgURL('default')} 1x, ${getImgURL('hidpi')} 2x`,
                media: `(min-width: ${threshold}px)`,
            },
            {
                srcSet: `${getImgURL('small')} 1x, ${getImgURL(
                    'smallHidpi',
                )} 2x`,
                media: `(max-width: ${threshold}px)`,
            },
        ];
    };

    if (false !== onClick) {
        return (
            <div onClick={onClick}>
                {true === placeholder && (
                    <img src={getImgURL()} alt="Click to insert" />
                )}
                {true !== placeholder && (
                    <Picture sources={getImgSrcSet()} alt="Click to edit" />
                )}
            </div>
        );
    }

    return (
        <Fragment>
            {'' === link && <Picture sources={getImgSrcSet()} />}
            {'' !== link && (
                <a href={link}>
                    <Picture sources={getImgSrcSet()} />
                </a>
            )}
        </Fragment>
    );
};

export default ImageDisplay;